import { Pipe, PipeTransform } from '@angular/core';
import * as moment from 'moment';

@Pipe({
  name: 'daterangefilter'
})
export class DaterangefilterPipe implements PipeTransform {

  transform(users: any[], userDate: any[]): any[] {
    let filteredArray = new Array();
    if (users && users.length && userDate.length === 2) {
      const startDate =new Date(moment(userDate[0]).format('MM-DD-YYYY'));
      const endDate = new Date(moment(userDate[1]).format('MM-DD-YYYY'));
      const sc = new Date(moment(users[0].RequestedDate).format('MM-DD-YYYY'));
      // tslint:disable-next-line: max-line-length
      filteredArray = users.filter(x => new Date(moment(x.RequestedDate).format('MMM-DD-YYYY')) >= startDate && new Date(moment(x.RequestedDate).format('MMM-DD-YYYY')) <= endDate);
      // filteredArray = users.filter(x => moment(x.scheduledDate).format('DD-MMM-YYYY') >= startDate && moment(x.scheduledDate).format('DD-MMM-YYYY') <= endDate);
      return filteredArray;
    } else {
      return users;
    }


  }
}
