import { Component, OnInit } from '@angular/core';
import { InfoService } from '../shared/info.service';
import { Router } from '@angular/router';
import { ToastrManager } from 'ng6-toastr-notifications';
import * as _ from "lodash";
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-update',
  templateUrl: './update.component.html',
  styleUrls: ['./update.component.css'],
  providers: [DatePipe]
})
export class UpdateComponent implements OnInit {
  // status = 'All';
  TravelStatus = 'Travel Approved';
  fulldetails: any[] = [];
  dtOptions: DataTables.Settings = {};
  GetTravelProfileInitial: any[] = [];
  datatable: boolean = false;
  filterArray: any[] = [];
  SearchId: string;
  GetGuid: any = {};
  GetGUIDs: any;

  // table: any = $('#table1');
  constructor(
    private informService: InfoService,
    private router: Router,
    private toaster: ToastrManager,
    public datepipe: DatePipe
  ) { }

  ngOnInit() {
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 10,
      paging: false,
      searching: false,
      info: false,
      ordering: false
    };

    this.informService.GetCurrentUser(() => {
      this.informService.GetTravelProfileDetails().then((res: any) => {
        this.GetTravelProfileInitial = res;
        if (this.GetTravelProfileInitial.length > 0) {
          this.router.navigate(['/UpdateTicket']);
        } else {
          this.router.navigate(['/MyProfile']);
          this.toaster.successToastr('Please update your profile');
        }
      });

      // setTimeout(function () {
      this.informService.GetTravelRequestFull(false).then((resp: any) => {
        if (resp && resp.length > 0) {
          this.datatable = false;
          this.fulldetails = resp;
          this.fulldetails.forEach(element => {
            // element.ReportingDate = this.datepipe.transform(element.ReportingDate, 'dd-MMM-yyyy')
            element.ReportingDate = new Date(element.ReportingDate);
          });
          this.filterArray = this.fulldetails.filter(x => x.TravelStatus == this.TravelStatus);
        } else {
          this.datatable = true;
        }
      });
      // }, 3000);
    });
  }
  // ngAfterViewInit() {
  //   this.dtOptions = {
  //     pagingType: 'full_numbers',
  //     pageLength: 10,
  //     paging: true,
  //     searching: true,
  //     info: false,
  //     ordering: false
  //   };
  // }
  ToViewDetailsUpdate(key: any, id: any) {
    // let temp = btoa(Id);
    this.router.navigate(['/NewTravelRequest', key, id]);
  }
  //  ToViewDetailsUpdate(key: any, x: any) {
  //   let Id = x.Id;
  //   this.informService.GetGUID(Id).then((res: any) => {
  //     this.GetGuid = res[0];
  //     // const id = this.GetGuid.GUID;
  //     this.GetGUIDs = this.GetGuid.GUID;
  //     if(this.GetGUIDs != null || this.GetGUIDs != undefined) {
  //       let id = this.GetGUIDs;
  //       this.router.navigate(['/NewTravelRequest', key, id]);
  //     }
  //   });
  // } 
  AdminViewtravel(Id: any) {
    const id = btoa(Id);
    this.router.navigate(['ViewTravelRequest', id]);
  }
  Filter(ev: any) {
    this.SearchId = '';
    this.TravelStatus = ev.target.value;
    if (this.TravelStatus == 'All') {
      this.filterArray = this.fulldetails;
    } else {
      this.filterArray = this.fulldetails.filter(x => x.TravelStatus == this.TravelStatus);
    }
  }
  Search(event: any) {
    let eve = event;
    if (eve != null || eve != undefined) {
      if (this.TravelStatus == 'All') {
        this.filterArray = this.fulldetails;
      } else {
        this.filterArray = this.fulldetails.filter(x => x.TravelStatus == this.TravelStatus);
      }
      // this.filterArray = this.flowFilter(this.filterArray, eve);
      this.filterArray = this.filterArray.filter(s => s.Id.toString().toLowerCase().indexOf(eve.toLowerCase()) >= 0 || s.EmployeeName.toString().toLowerCase().indexOf(eve.toLowerCase()) >= 0 || s.EmployeeID.toString().toLowerCase().indexOf(eve.toLowerCase()) >= 0 || s.Project.toString().toLowerCase().indexOf(eve.toLowerCase()) >= 0 || s.ReportingDate.toString().toLowerCase().indexOf(eve.toLowerCase()) >= 0 || s.TravelPurpose.toString().toLowerCase().indexOf(eve.toLowerCase()) >= 0 || s.TravelType.toString().toLowerCase().indexOf(eve.toLowerCase()) >= 0 || s.TravelStatus.toString().toLowerCase().indexOf(eve.toLowerCase()) >= 0);
      // this.filterArray = _.filter(this.filterArray, item => {
      //   return _.includes(eve, item['Id']) || _.includes(eve, item['EmployeeName']) || _.includes(eve, item['EmployeeID']) || _.includes(eve, item['Project']) || _.includes(eve, item['ReportingDate']) || _.includes(eve, item['TravelPurpose']) || _.includes(eve, item['TravelType']) || _.includes(eve, item['TravelStatus']);
      //   // return _.includes(eve, item['Id']) && _.includes(eve, item['EmployeeName']) && _.includes(eve, item['EmployeeID']) && _.includes(eve, item['Project']) && _.includes(eve, item['ReportingDate']) && _.includes(eve, item['TravelPurpose']) && _.includes(eve, item['TravelType']) && _.includes(eve, item['TravelStatus']);
      // })
    
    }
    if (eve == '') {
      if (this.TravelStatus == 'All') {
        this.filterArray = this.fulldetails;
      } else {
        this.filterArray = this.fulldetails.filter(x => x.TravelStatus == this.TravelStatus);
      }
    }

  }
    flowFilter(array, substr) {
      return _.filter(array, _.flow(
      _.identity,
      _.values,
      _.join,
      _.toLower,
      _.partialRight(_.includes, substr)
    ));
  }
}
