import { Component, OnInit, TemplateRef } from "@angular/core";
import { Router } from "@angular/router";
import { InfoService } from "../shared/info.service";
import { BsModalService, BsModalRef } from "ngx-bootstrap/modal";
import { BsDatepickerConfig } from "ngx-bootstrap/datepicker";
import { Dependents } from "../shared/depend";
import { NgForm } from "@angular/forms";
import * as moment from "moment";
import { ToastrModule, ToastrManager } from "ng6-toastr-notifications";

@Component({
  selector: "app-dependends",
  templateUrl: "./dependends.component.html",
  styleUrls: ["./dependends.component.css"],
})
export class DependendsComponent implements OnInit {
  Dependents: any = {};
  dtOptions: DataTables.Settings = {};
  bsModalRef: BsModalRef;
  config = {
    backdrop: true,
    class: "modal-lg",
    ignoreBackdropClick: true,
  };
  configs = {
    backdrop: true,
    class: "modal-md",
    ignoreBackdropClick: true,
  };
  dependentsArray: any[] = [];
  GetByIdArray: any = {};
  bsConfig: Partial<BsDatepickerConfig> = {
    dateInputFormat: this.infoService.DateFormate,
    adaptivePosition: true,
    showWeekNumbers: false,
  };
  isEdit = false;
  Id: any;
  minDate = new Date();
  datatable: boolean = false;
  ProxyEmail: any;
  ProxyName: any;
  constructor(
    public infoService: InfoService,
    private router: Router,
    private modalService: BsModalService,
    private toaster: ToastrManager
  ) {
    this.ProxyEmail = this.infoService.getLocalStorage("ProxyEmail");
    if (this.ProxyEmail != null || this.ProxyEmail != undefined) {
      this.ProxyName = this.ProxyEmail;
    }
  }

  ngOnInit() {
    this.dtOptions = {
      pagingType: "full_numbers",
      pageLength: 10,
      paging: true,
      searching: true,
      info: false,
      ordering: false,
    };
    this.infoService.GetCurrentUser(() => {
      if (this.ProxyName != null || this.ProxyName != undefined) {
        this.infoService
          .GetDependentsProxy(this.ProxyName)
          .then((resp: any) => {
            this.dependentsArray = resp;
            if (this.dependentsArray && this.dependentsArray.length > 0) {
              this.datatable = false;
              this.dependentsArray.forEach((item) => {
                item.DOBPassport = new Date(item.DOBPassport);
                item.PassportExpiryDate = new Date(item.PassportExpiryDate);
              });
            } else {
              this.datatable = true;
            }
          });
      } else {
        this.GetDependentsList();
      }
    });
  }
  GetDependentsList() {
    this.infoService.GetDependents().then((res: any) => {
      this.dependentsArray = res;
      if (this.dependentsArray && this.dependentsArray.length > 0) {
        this.datatable = false;
        this.dependentsArray.forEach((item) => {
          item.DOBPassport = new Date(item.DOBPassport);
          item.PassportExpiryDate = new Date(item.PassportExpiryDate);
        });
      } else {
        this.datatable = true;
      }
    });
  }
  AddDependends(templateDependend: TemplateRef<any>) {
    this.bsModalRef = this.modalService.show(templateDependend, this.config);
  }
  add(form: NgForm) {
    // this.Dependents = {};
    this.isEdit = false;
    if (form.valid) {
      const Obj = {
        Title: "",
        GivenName: this.Dependents.GivenName.toUpperCase(),
        SurName: this.Dependents.SurName.toUpperCase(),
        PassportNo: this.Dependents.passportNo,
        DOBPassport: this.Dependents.dob,
        PassportExpiryDate: this.Dependents.expiry,
        Gender: this.Dependents.gender,
        Relationship: this.Dependents.relationship,
      };
      this.infoService.InsertDependents(Obj).then((res: any) => {
        if (res) {
          this.toaster.successToastr("Dependent added successfully");
          this.ngOnInit();
          this.Dependents = {};
          this.bsModalRef.hide();
        } else {
          this.toaster.errorToastr("Dependent added in error");
        }
      });
    }
  }
  update(form: NgForm) {
    this.isEdit = true;
    if (form.valid) {
      const Obj = {
        Title: "",
        Id: this.GetByIdArray.Id,
        GivenName: this.Dependents.GivenName.toUpperCase(),
        SurName: this.Dependents.SurName.toUpperCase(),
        PassportNo: this.Dependents.passportNo,
        DOBPassport: this.Dependents.dob,
        PassportExpiryDate: this.Dependents.expiry,
        Gender: this.Dependents.gender,
        Relationship: this.Dependents.relationship,
      };
      this.infoService.UdateDependents(Obj).then((res: any) => {
        if (res) {
          this.toaster.successToastr("Dependent updated successfully");
          this.bsModalRef.hide();
          this.isEdit = false;
          this.ngOnInit();
          this.Dependents = {};
        } else {
          this.toaster.errorToastr("Dependent updated in error");
        }
      });
    }
  }
  Edit(x: any, templateDependend: TemplateRef<any>) {
    this.bsModalRef = this.modalService.show(templateDependend, this.config);
    this.isEdit = true;
    let Id = x.Id;
    this.infoService.GetByDependentsId(Id).then((res: any) => {
      if (res) {
        this.GetByIdArray = res;
        this.Dependents.GivenName = res.GivenName;
        this.Dependents.SurName = res.SurName;
        this.Dependents.passportNo = res.PassportNo;
        this.Dependents.dob = new Date(res.DOBPassport);
        this.Dependents.expiry = new Date(res.PassportExpiryDate);
        this.Dependents.relationship = res.Relationship;
        this.Dependents.gender = res.Gender;
        this.ngOnInit();
      }
    });
  }
  Delete(x: any, confirmTemplate: TemplateRef<any>) {
    this.bsModalRef = this.modalService.show(confirmTemplate, this.configs);
    this.Id = x.Id;
  }
  deleteConfirm() {
    this.infoService.DeleteDependents(this.Id).then((res: any) => {
      if (res) {
        this.toaster.successToastr("Dependent deleted successfully");
        this.GetDependentsList();
        this.ngOnInit();
        this.Dependents = {};
        this.bsModalRef.hide();
      } else {
        this.toaster.errorToastr("Dependent deleted in error");
      }
    });
  }
  canceldepend() {
    this.ngOnInit();
    this.bsModalRef.hide();
    this.isEdit = false;
    this.Dependents = {};
  }
  hide() {
    this.bsModalRef.hide();
    this.isEdit = false;
    this.Dependents = {};
  }
  number(event) {
    const charCode = event.which ? event.which : event.keyCode;
    if (charCode <= 126) {
      return false;
    }
    return true;
  }
}
