import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DependendsComponent } from './dependends/dependends.component';
import { CityComponent } from './city/city.component';
import { EditTravelComponent } from './edit-travel/edit-travel.component';
import { FeedbackRestrictionComponent } from './feedback-restriction/feedback-restriction.component';
import { MyapprovalComponent } from './myapproval/myapproval.component';
import { MyprofileComponent } from './myprofile/myprofile.component';
import { NewtravelRequestComponent } from './newtravel-request/newtravel-request.component';
import { ProxyuserComponent } from './proxyuser/proxyuser.component';
import { ReportsComponent } from './reports/reports.component';
import { TravelRequestComponent } from './travel-request/travel-request.component';
import { UpdateComponent } from './update/update.component';
import { ViewTicketDetailsComponent } from './view-ticket-details/view-ticket-details.component';
import { ViewTicketComponent } from './view-ticket/view-ticket.component';
import { ViewTravelRequestComponent } from './view-travel-request/view-travel-request.component';
import { VisaOfficerLocationComponent } from './visa-officer-location/visa-officer-location.component';
import { VisaProcessComponent } from './visa-process/visa-process.component';
import { VisaValiditiesComponent } from './visa-validities/visa-validities.component';
import { ManageCountryComponent } from './manage-country/manage-country.component';
import { AuthGuard } from './shared/guard/auth.guard';

const routes: Routes = [
  {
    path: '',
    component: TravelRequestComponent
  },
  {
    path: 'MyApproval',
    component: MyapprovalComponent
  },
  {
    path: 'NewTravelRequest',
    component: NewtravelRequestComponent
  },
  {
    path: 'EditTravelRequest/:key/:id',
    component: EditTravelComponent
  },
  {
    path: 'NewTravelRequest/:key/:id',
    component: NewtravelRequestComponent
  },
  {
    path: 'NewTravelRequest/:key',
    component: NewtravelRequestComponent
  },
  {
    path: 'MyProfile',
    component: MyprofileComponent
  },
  {
    path: 'Dependends',
    component: DependendsComponent
  },
  {
    path: 'ViewTravelRequest',
    component: ViewTravelRequestComponent
  },
  {
    path: 'ViewTravelRequest/:id',
    component: ViewTravelRequestComponent
  },
  {
    path: 'UpdateTicket',
    component: UpdateComponent
  },
  {
    path: 'UpdateTicket/:id',
    component: UpdateComponent
  },
  {
    path: 'ProxyUser',
    component: ProxyuserComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'AllReport',
    component: ReportsComponent
  },
  {
    path: 'VisaValidities',
    component: VisaValiditiesComponent
  },
  {
    path: 'viewticket',
    component: ViewTicketComponent
  },
  {
    path: 'viewticketdetails/:id',
    component: ViewTicketDetailsComponent
  },
  {
    path: 'feedbackrestriction',
    component: FeedbackRestrictionComponent
  },
  {
    path: 'officerlocation',
    component: VisaOfficerLocationComponent
  },
  {
    path: 'visaprocess',
    component: VisaProcessComponent
  },
  {
    path: 'city',
    component: CityComponent
  },
  {
    path: 'country',
    component: ManageCountryComponent
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
