import { Component, OnInit, TemplateRef } from '@angular/core';
import { Router } from '@angular/router';
import { cloneDeep } from 'lodash';
import { ToastrManager } from 'ng6-toastr-notifications';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { InfoService } from '../shared/info.service';

@Component({
  selector: 'app-city',
  templateUrl: './city.component.html',
  styleUrls: ['./city.component.css']
})
export class CityComponent implements OnInit {
  dtOptions: DataTables.Settings = {};
  sourceData: any[] = [];
  filteredData: any[] = [];
  searchKey: string = '';
  config = {
    backdrop: true,
    class: "modal-xl",
    ignoreBackdropClick: true,
  };
  bsModalRef: BsModalRef;
  mappingData: any = {}
  locations: any;
  users: any;
  id: any;
  deletingItem: any;
  city: any = {};
  country: any = {};
  countryList: any;

  constructor(
    private infoService: InfoService,
    private router: Router,
    private toaster: ToastrManager,
    private modalService: BsModalService,
  ) { }

  ngOnInit() {
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 10,
      paging: false,
      searching: false,
      info: false,
      ordering: false
    };

    this.infoService.GetCurrentUser(() => {
      this.infoService.GetTravelProfileDetails().then((response: any) => {
        if (!(response && response.length > 0)) {
          this.router.navigate(['/MyProfile']);
          this.toaster.successToastr('Please update your profile');
        }
      });
      this.getCountries();
      this.getData();
    });
  }

  private getCountries() {
    this.infoService.getCountry().then((res: any) => {
      this.countryList = res;
    });
  }

  getData() {
    this.infoService.GetAllLocations().then((result: any) => {
      if (result && result.length > 0) {
        this.sourceData = result;
        this.filteredData = cloneDeep(this.sourceData)
      }
    });
  }

  filterKeyChanged() {
    this.searchKey = '';
  }

  searchKeyChanged() {
    if (this.searchKey && this.searchKey.trim().length > 0) {
      this.filteredData = this.filteredData.filter(s =>
        (s.city_code && (s.city_code.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.location && (s.location.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.country && (s.country.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.region && (s.region.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0))
      );
    } else {
      this.filteredData = cloneDeep(this.sourceData)
    }
  }

  openPopUp(templatePopup: TemplateRef<any>) {
    this.bsModalRef = this.modalService.show(templatePopup, this.config);
  }

  submit(form) {
    const isExist = this.sourceData.find(item => item.location == this.city.cityName && item.ID == this.city.ID && item.city_code == this.city.cityCode && item.country == this.city.country);
    if (isExist) {
      this.toaster.errorToastr("Already exist");
      return;
    }
    if (form.valid) {
      if (this.city.ID) {
        const payload = {
          city_code: this.city.cityCode,
          location: this.city.cityName,
          country_id: this.countryList.find(item => item.Country === this.city.country).ID,
          country: this.city.country,
          region: this.countryList.find(item => item.Country === this.city.country).Region,
          region_id: this.countryList.find(item => item.Country === this.city.country).RegionId,
        }

        this.infoService
          .updateCity(payload, this.city.ID)
          .then(() => {
            this.getData();
            this.cancel();
            this.toaster.successToastr('Updated successfully');
          });

      } else {
        const payload = {
          city_code: this.city.cityCode,
          location: this.city.cityName,
          country_id: this.countryList.find(item => item.Country === this.city.country).ID,
          country: this.city.country,
          region: this.countryList.find(item => item.Country === this.city.country).Region,
          region_id: this.countryList.find(item => item.Country === this.city.country).RegionId,
          is_active: true,
        }

        this.infoService
          .InsertLocation(payload)
          .then((res: any) => {
            this.getData();
            this.cancel();
            this.toaster.successToastr('Added successfully');
          });
      }
    }
  };

  cancel() {
    this.city = {};
    this.country = {};
    this.bsModalRef.hide();
  }

  edit(item, MapOfficerPopup: TemplateRef<any>) {
    this.city.ID = item.ID;
    this.city.cityName = item.location,
      this.city.cityCode = item.city_code,
      this.city.country_id = item.country_id,
      this.city.country = item.country,
      this.city.region = item.region,
      this.city.is_active = item.is_active,
      this.bsModalRef = this.modalService.show(MapOfficerPopup, this.config);
  }

  removeConfirmation(item: any, confirmTemplate: TemplateRef<any>) {
    if (!item.is_active) {
      this.infoService.GetCountryById(item.country_id).then((result: any) => {
        if (!result.IsActive) {
          this.toaster.errorToastr("Please activate it's country ("+ item.country + ")");
        } else {
        this.bsModalRef = this.modalService.show(confirmTemplate, this.config);
        this.deletingItem = item;
        }
      });
    } else {
      this.bsModalRef = this.modalService.show(confirmTemplate, this.config);
      this.deletingItem = item;
    }
  }

  remove() {
    const payload: any = {
      is_active: !this.deletingItem.is_active
    }
    this.infoService.updateCity(payload, this.deletingItem.ID).then(() => {
      this.getData();
      this.denyRemoval();
      this.deletingItem.is_active ? this.toaster.successToastr('Deactivated successfully') : this.toaster.successToastr('Activated successfully');
    });
  }

  denyRemoval() {
    this.bsModalRef.hide();
    this.id = null;
  }

  submitCountry(form) {
    const isExist = this.countryList.find(item => item.Country == this.country.countryName);
    if (isExist) {
      this.toaster.errorToastr("Already exist");
      return;
    }
    if (form.valid) {
        const payload = {
          Country: this.country.countryName,
          Region: this.country.region,
        }
        this.infoService
          .InsertCountry(payload)
          .then((res: any) => {
            this.getCountries();
            this.cancel();
            this.toaster.successToastr('Added successfully');
          });
    }
  };

}
