import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { cloneDeep, size } from 'lodash';
import { BsDatepickerConfig } from 'ngx-bootstrap/datepicker';
import { DaterangefilterPipe } from '../shared/daterangefilter.pipe';
import { InfoService } from '../shared/info.service';
import * as XLSX from 'xlsx';
@Component({
  selector: 'app-reports',
  templateUrl: './reports.component.html',
  styleUrls: ['./reports.component.css']
})
export class ReportsComponent implements OnInit {
  TravelStatus = '';
  cluster = '';
  dtOptions: DataTables.Settings = {};
  bsConfig: Partial<BsDatepickerConfig> = { dateInputFormat: 'DD-MMM-YYYY', adaptivePosition: true, showWeekNumbers: false };
  @Input() reportStatus: string = "";
  userObj: any = {};
  datatable: boolean = false;
  ProxyEmail: any;
  ProxyName: any;
  GetTravelRequestReport: any[] = [];
  status: any;
  minDate: Date;
  fulldetails: any[] = [];
  filterArray: any[] = [];
  columnData: string[] = [];
  reportDetails: any[] = [];
  filterPipe: any;
  reporArray: any[];
  dateRange: any;
  clusterData: any[] = [];
  DeliveryUnit: any;
  fileName = 'ExcelSheet.xlsx';
  array1 = new Array();
  array2 = new Array();
  n = 2; //Total table
  tables: any[]
  wss: any;

  constructor(
    public informService: InfoService,
    private activatedRoute: ActivatedRoute
  ) {
    this.ProxyEmail = this.informService.getLocalStorage('ProxyEmail');
    if (this.ProxyEmail != null || this.ProxyEmail != undefined) {
      this.ProxyName = this.ProxyEmail;
    }
    this.status = this.activatedRoute.snapshot.params['status'];
    this.minDate = new Date();
  }

  ngOnInit() {
    this.userObj = this.informService.Current;
    this.informService.GetCurrentUser(() => {
    });
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 10,
      paging: true,
      searching: false,
      info: true,
      ordering: false
    };
    this.informService.getCluster().then((res: any) => {
      this.clusterData = res
    })
    this.informService.GetTravelRequestReport().then((resp: any) => {
      if (resp) {
        this.datatable = false;
        this.fulldetails = resp.fullTravelDetails;
        this.reporArray = cloneDeep(this.fulldetails)
        // var result = this.fulldetails.reduce(function (res, obj) {
        //   return (Object.keys(obj).length >= Object.keys(res).length) ? obj : res;
        // });
        // let index = this.fulldetails.findIndex(el => el === result)
        // this.columnData = Object.keys(this.fulldetails[index]);
        // this.columnData.forEach(el => {
        //   const filteredData = el.replace(/([A-Z])/g, ' $1').trim()
        //   this.reportDetails.push(filteredData)
        // })
        this.filterArray = this.fulldetails
      } else {
        this.datatable = true;
      }
    });

    // for ( let x=1; x<=this.n; x++ ) {
    //   this.array1[x-1] = x;
    //   this.array2[x-1] = x + 'th';
    //   }
  }
  number(event) {
    const charCode = event.which ? event.which : event.keyCode;
    if (charCode <= 126) {
      return false;
    }
    return true;
  }
  Filter(ev: any) {
    this.TravelStatus = ev.target.value;
    if (this.TravelStatus === 'All') {
      this.filterArray = this.reporArray;
    } else {
      this.filterArray = this.fulldetails.filter(x => x.TravelStatus == this.TravelStatus);
    }
  }
  FilterCluster(cluster: any) {
    this.filterArray = this.fulldetails.filter(x => x.DeliveryUnit == this.cluster);
  }
  onChange(eve: any) {
    this.filterPipe = new DaterangefilterPipe();
    this.filterArray = this.filterPipe.transform(this.filterArray, eve);
  }

  clear() {
    this.filterArray = this.reporArray;
    this.TravelStatus = '';
    this.dateRange = '';
    this.cluster = ''
  }


  exportexcel() {
    let wsss
    const fileName = 'Sheet1.xlsx';
    const wb: XLSX.WorkBook = XLSX.utils.book_new();

    const ws: XLSX.WorkSheet = XLSX.utils.json_to_sheet(this.filterArray);
    XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');

    this.filterArray.forEach(element => {
      if (element.travelAdvanceArray.length > 0) {
        let wsss: XLSX.WorkSheet = XLSX.utils.json_to_sheet(element.travelAdvanceArray);
      }
      XLSX.utils.book_append_sheet(wb, wsss, 'Sheet2');
    });

    XLSX.writeFile(wb, this.fileName);

  }

}
