  TravelRequestRejectedRescheduleUpdate(form: NgForm) {
    if (form.valid) {
      this.clicked = true;
      for (let i = 0; i < this.tripDeatilsArray.length; i++) {
        if (
          this.tripDeatilsArray[i].TicketStatus == "Reschedule Requested" ||
          this.tripDeatilsArray[i].TicketStatus == "Cancel Requested"
        ) {
          this.informService
            .GetAllVersionsRoute(this.tripDeatilsArray[i].Id)
            .then((response: any) => {
              for (let v = 1; v < response.length; v++) {
                if (
                  this.tripDeatilsArray[i].TicketStatus !=
                  response[v].TicketStatus
                ) {
                  let Object = {
                    Id: this.tripDeatilsArray[i].Id,
                    Form: response[v].Form,
                    To: response[v].To,
                    TravelDate: response[v].TravelDate,
                    FirstPreferance: response[v].FirstPreferance,
                    SecondPreferance: response[v].SecondPreferance,
                    TicketStatus: response[v].TicketStatus,
                    ActualDate: response[v].ActualDate,
                    ReferenceLeg: response[v].ReferenceLeg,
                  };
                  this.RouteResults.push(Object);
                  break;
                }
              }
            });
        }
        if (
          this.tripDeatilsArray[i].TicketStatus == "Open" &&
          (this.tripDeatilsArray[i].ReferenceLeg != null ||
            this.tripDeatilsArray[i].ReferenceLeg != undefined)
        ) {
          let Objects = {
            TicketStatus: "Cancelled",
            Id: this.tripDeatilsArray[i].Id,
          };
          this.informService.TravelRequestRejectedUpdateVersion(Objects).then();
        }
        if (this.tripDeatilsArray[i].TicketStatus == "Open") {
          this.informService
            .GetAllVersionsRoute(this.tripDeatilsArray[i].Id)
            .then((respo: any) => {
              if (respo.length <= 1) {
                let Objects = {
                  TicketStatus: "Cancelled",
                  Id: this.tripDeatilsArray[i].Id,
                };
                this.informService
                  .TravelRequestRejectedUpdateVersion(Objects)
                  .then();
              }
            });
        }
      }
      this.informService.GetAllVersions(this.rgid).then((result: any) => {
        this.TravelVersion = result;
        for (let s = 0; s < this.TravelVersion.length; s++) {
          if (this.TravelVersion[s].TravelStatus != "Reschedule Requested") {
            this.Obj = {
              Id: this.rgid,
              TravelStatus: this.TravelVersion[s].TravelStatus,
              ApprovalAuthority: this.TravelVersion[s].ApprovalAuthority,
              ApproversSuperior: this.TravelVersion[s].ApproversSuperior,
              TravelApprover: this.TravelVersion[s].TravelApprover,
              IsDeviationRequest: this.TravelVersion[s].IsDeviationRequest,
              ReportingDate: this.TravelVersion[s].ReportingDate,
              Remarks: this.comments,
            };
            break;
          }
        }
      });
      setTimeout(() => {
        const RouteObject = {
          tripDeatilsArray: this.RouteResults,
        };
        this.informService
          .TravelRequestRejectedUpdate(this.Obj, RouteObject)
          .then((res: any) => {
            this.ngOnInit();
            this.Obj = null;
            this.bsModalRef.hide();
            this.clicked = false;
            this.toaster.successToastr("Reschedule Travel Request is Rejected");
            this.router.navigate(["/"]);
          });
      }, 3000);
    }
  }

  GetTickets() {
    this.informService.GetTravelTicket(this.rgid).then((res: any) => {
      this.TicketFiles = res;
      this.TicketFiles.forEach((element) => {
        let temp: any = {};
        temp.FileName = element.FileName;
        temp.CreatedTime = element.CreatedTime;
        temp.CanDisplay = false;
        temp.CanRemove = false;
        this.canAttach.push(temp);
      });
      this.filesData = JSON.parse(this.userObject.TravelRequest.FileName);

      for (let index = 0; index < this.TicketFiles.length; index++) {
        const ele = this.TicketFiles[index];
        if (this.filesData && this.filesData.length > 0) {
          if (
            this.filesData.findIndex((e) => e.FileName === ele.FileName) > -1
          ) {
            ele.CanDisplay = true;
            ele.CanRemove = false;
          } else {
            ele.CanDisplay = true;
            ele.CanRemove = true;
          }
        } else {
          ele.CanDisplay = true;
          ele.CanRemove = true;
        }
      }
    });
  }
