import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ToastrManager } from 'ng6-toastr-notifications';
import { InfoService } from '../shared/info.service';

@Component({
  selector: 'app-view-ticket',
  templateUrl: './view-ticket.component.html',
  styleUrls: ['./view-ticket.component.css'],
})
export class ViewTicketComponent implements OnInit {
  dtOptions: DataTables.Settings = {};
  sourceData: any[] = [];
  filteredData: any[] = [];
  filterKey: string = 'Travel Approved';
  searchKey: string = '';
  filterDropdown: any[] = [ 
    {value: "Travel Approved", displayName: "Travel Approved"},
    {value: "Booked", displayName: "Booked"},
    {value: "Reschedule Approved", displayName: "Reschedule Approved"},
    {value: "Rescheduled", displayName: "Rescheduled"},
    {value: "Cancellation Requested", displayName: "Cancellation Requested"},
    {value: "Cancelled", displayName: "Cancelled"},
    {value: "All", displayName: "All"}
  ];

  constructor(
    private infoService: InfoService,
    private router: Router,
    private toaster: ToastrManager,
  ) { }

  ngOnInit() {
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 10,
      paging: false,
      searching: false,
      info: false,
      ordering: false
    };

    this.infoService.GetCurrentUser(() => {
      this.infoService.GetTravelProfileDetails().then((response: any) => {
        if (!(response && response.length > 0)) {
          this.router.navigate(['/MyProfile']);
          this.toaster.successToastr('Please update your profile');
        }
      });

      this.infoService.GetTravelRequestFull(true).then((result: any) => {
        if (result && result.length > 0) {
          this.sourceData = result;
          this.sourceData.forEach(element => {
            element.ReportingDate = new Date(element.ReportingDate);
          });
          this.filteredData = this.sourceData.filter(x => x.TravelStatus == this.filterKey);
        }
      });
    });
  }

  view(id: any) {
    this.router.navigate(['/viewticketdetails', id]);
  }

  filterKeyChanged() {
    this.searchKey = '';
    this.filteringData();
  }

  searchKeyChanged() {
    if (this.searchKey && this.searchKey.trim().length > 0) {
      this.filteringData();
      this.filteredData = this.filteredData.filter(s =>
        (s.Id && (s.Id.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.EmployeeName && (s.EmployeeName.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.EmployeeID && (s.EmployeeID.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.Project && (s.Project.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.ReportingDate && (s.ReportingDate.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.TravelPurpose && (s.TravelPurpose.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.TravelType && (s.TravelType.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.TravelStatus && (s.TravelStatus.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0))
      );
    } else {
     this.filteringData();
    }
  }

  filteringData() {
    if (this.filterKey == 'All') {
      this.filteredData = this.sourceData;
    } else {
      this.filteredData = this.sourceData.filter(x => x.TravelStatus == this.filterKey);
    }
  }
}
