import {
  Component,
  OnInit,
  ViewChild,
  ElementRef,
  TemplateRef,
} from "@angular/core";
import { InfoService } from "../shared/info.service";
import { BsDatepickerConfig } from "ngx-bootstrap/datepicker/ngx-bootstrap-datepicker";
import { Router, ActivatedRoute } from "@angular/router";
import { BsModalRef, BsModalService } from "ngx-bootstrap/modal";
import { ToastrManager } from "ng6-toastr-notifications";
import { saveAs } from "file-saver";

@Component({
  selector: 'app-view-ticket-details',
  templateUrl: './view-ticket-details.component.html',
  styleUrls: ['./view-ticket-details.component.css']
})
export class ViewTicketDetailsComponent implements OnInit {
  config = {
    backdrop: true,
    class: "modal-xl",
    ignoreBackdropClick: true,
  };
  isEdit = false;
  public userObj: any = {};
  submitted = false;
  isShow = false;
  isShowDomestic = false;
  tripDeatilsArray: any[] = [
    {
      Form: "",
      To: "",
      TravelDate: null,
      FirstPreferance: "",
      SecondPreferance: "",
      fromDisable: false,
      toDisable: false,
      TravelDateDisable: false,
    },
  ];
  AdvanceDetails: any[] = [
    {
      Currency: "",
      Card: 0,
      Cash: 0,
      Amount: 0,
      carddisable: false,
      cashdisable: false,
    },
  ];
  isDependent = false;
  DependentArray: any[] = [];
  minDate = new Date();
  today = new Date();
  isAdvance = false;
  isTableShow = false;
  isDeviation = false;
  bsConfig: Partial<BsDatepickerConfig> = {
    dateInputFormat: this.informService.DateFormate,
    adaptivePosition: true,
    showWeekNumbers: false,
  };
  dtOptions: DataTables.Settings = {};
  bsModalRef: BsModalRef;
  locationArrays: any[] = [];
  userObject: any = {};
  DependentArrayView: any[] = [];
  DependentAdd: any[] = [];
  RId = 0;
  Appkey: any = "UpdateTicket";
  projectoaid: any[] = [];
  Hrinfo: any[] = [];
  BookingNames: any[] = [];
  common: any[] = [];
  TPSuperior: any = {};
  TPSuperSuperior: any = {};
  purposeOfProject: { Id: number; TCId: number; name: string }[] = [];
  Depart: any[] = [];
  Delivery: any = {};
  DeliverySource: {
    Id: number;
    name: string;
    department_type_name: string;
  }[] = [];
  TicketFiles: any[] = [];
  rgid: any;
  IsEditMode = false;
  updateEdit = false;
  CurrancyType: any[] = [];
  Costdetails: any[] = [];
  el: any = {};
  EditApprovalAuthority: any;
  EditApproversSuperior: any;
  @ViewChild("file") file: ElementRef;
  PreviousDetails: any[] = [];
  DM: any;
  TP: any;
  DMSuperior: any;
  DMSuperiorSuper: any;
  filesData: any[] = [];
  isVisaSubmitted: boolean;
  isPasswordExpireInSixMnt: boolean = false;
  DependSixMnt: boolean = false;
  PassportExpDate: Date;
  selectedDependantIndex: any;
  firstDependentIndex: any;
  clientDebitFilesUploaded: any = [];

  constructor(
    public informService: InfoService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private toaster: ToastrManager,
    private modalService: BsModalService,
  ) {
    this.userObj = this.informService.Current;
    this.minDate = new Date();
    this.RId = this.activatedRoute.snapshot.params["id"];
  }
  ngOnInit() {
    this.userObj.AccommodationRequired = "";
    this.userObj.Project = "";
    this.userObj.ApprovalAuthority = "";
    this.userObj.ApproversSuperior = "";
    this.userObj.TravelCategory = "";
    this.userObj.TravelPurpose = "";
    this.userObj.TravelMeal = "";
    this.userObj.TravelType = "";
    this.userObj.IdentityProof = "";
    this.userObj.IDProofNumber = "";
    this.userObj.VisaStatus = "";
    this.userObj.bsValue = null;
    this.userObj.TypeofVisa = "";
    this.userObj.VisaNo = "";
    this.userObj.TripType = "";
    this.userObj.DeviationReason = "";
    this.userObj.ReportingDate = null;
    this.userObj.Travel_x0020_For = "Self";
    this.userObj.ClientDebit = "false";
    this.userObj.AdvanceRequired = "false";
    this.informService.GetCurrentUser(() => {

      this.informService.GetDepertName().then((res: any) => {
        this.Depart = res;
      });
      this.informService.GetCurrency().then((res: any) => {
        this.CurrancyType = res;
      });
      this.informService.GetHrInfo().then((res: any) => {
        this.Hrinfo = res;
      });
      this.informService.GetBookingNames().then((res: any) => {
        this.BookingNames = res;
        let checkDate = new Date();
        this.informService.GetProjectoid().then(() => {
          this.projectoaid = this.informService.projectoaidlist;
          this.projectoaid.forEach((element) => {
            element.deploymentdate__c = new Date(element.deploymentdate__c);
            element.enddate__c = new Date(element.enddate__c);
          });

          for (var i = 0; i < this.BookingNames.length; i++) {
            var ele = this.BookingNames[i];
            for (var j = 0; j < this.projectoaid.length; j++) {
              var res = this.projectoaid[j];
              if (
                ele.project_id == res.bookingprojectoaid__c &&
                checkDate >= res.deploymentdate__c &&
                checkDate <= res.enddate__c &&
                this.common.findIndex((e) => e.project_id == ele.project_id) < 0
              ) {
                this.common.push(ele);

              }
            }
          }
          this.common.forEach((element) => {
            element.ProjectList =
              element.projectoraclecode__c +
              " " +
              "-" +
              " " +
              element.project_name;
          });
        });
      });
      this.informService.GetLocation().then((res: any) => {
        this.locationArrays = res;
      });
      this.GetDependentsList();
      this.informService.GetTravelProfileDetails().then((res: any) => {
        if (res && res.length > 0) {
          (this.userObj.GivenName = res[0].GivenName),
            (this.userObj.SurName = res[0].SurName),
            (this.userObj.PassportNo = res[0].PassportNo),
            (this.userObj.DateofBirth = new Date(res[0].DOBPassport)),
            (this.userObj.PassportExpiryDate = new Date(res[0].PassportExpiryDate)),
            (this.userObj.PassportExpDate = new Date(res[0].PassportExpDate)),
            (this.userObj.Mobile = res[0].Mobile);
          this.PassportExpDate = this.userObj.PassportExpDate.setMonth(this.userObj.PassportExpDate.getMonth() - 6);
          this.userObj.PANNumber = res[0].PANNumber;
        }

      });
    });
    this.userObj = this.informService.Current;
    this.dtOptions = {
      pagingType: "full_numbers",
      pageLength: 10,
      paging: false,
      searching: false,
      info: false,
      ordering: false,
    };

    if (this.RId != null && this.RId != undefined) {
      this.IsEditMode = true;

      if (
        this.Appkey == "UpdateTicket" ||
        this.Appkey == "Mytravel" ||
        this.Appkey == "approval"
      ) {
        this.updateEdit = true;
      }
      this.isEdit = true;

      this.informService.GetTravelRequestById(this.RId).then((res: any) => {
        this.informService.GetHrInfo().then((resps: any) => {
          this.Hrinfo = resps;
        });
        this.userObject = res;
        if (this.userObject.TravelRequest.ClientDebitFileName &&
          JSON.parse(this.userObject.TravelRequest.ClientDebitFileName).length > 0) {
          this.clientDebitFilesUploaded = JSON.parse(this.userObject.TravelRequest.ClientDebitFileName);
        }
        this.informService
          .DobSet(res.TravelRequest.EmployeeID)
          .then((resp: any) => {
            this.userObj.DOB = new Date(resp[0].dob);
            this.userObj.Age = this.CalculateAge(resp[0].dob);
          });
        res.TravelRequest.DateofBirth = new Date(res.TravelRequest.DateofBirth);
        if (res.TravelRequest.ReportingDate == null) {
          res.TravelRequest.ReportingDate = "";
        } else {
          res.TravelRequest.ReportingDate = new Date(res.TravelRequest.ReportingDate);
        }
        if (res.TravelRequest.VisaStatus === "Submitted" || res.TravelRequest.VisaStatus === "On-arrival" || res.TravelRequest.VisaStatus === "Return to home country") {
          this.isVisaSubmitted = true;
        }
        if (res.TravelRequest.PassportExpiryDate == null) {
          res.TravelRequest.PassportExpiryDate = "";
        } else {

          res.TravelRequest.PassportExpiryDate = new Date(res.TravelRequest.PassportExpiryDate);
        }
        if (res.TravelRequest.PassportExpDate == null) {
          res.TravelRequest.PassportExpDate = "";
        } else {

          res.TravelRequest.PassportExpDate = new Date(res.TravelRequest.PassportExpDate);

        }
        if (
          res.TravelRequest.InvoiceDate == null ||
          res.TravelRequest.InvoiceDate == "" ||
          res.TravelRequest.InvoiceDate == "1970-01-01T00:00:00Z"
        ) {
          res.TravelRequest.InvoiceDate = null;
        } else {
          res.TravelRequest.InvoiceDate = new Date(res.TravelRequest.InvoiceDate);
        }
        this.userObj = res.TravelRequest;
        if (this.Appkey === "UpdateTicket" && this.userObj.TravelMode == null) {
          this.userObj.isNotBooked = true;
        }
        if (this.Appkey === "UpdateTicket") {
          this.informService.IsAdmin(this.userObj.Title).then((result: boolean) => {
            this.userObj.isAdmin = result;
          });
        }
        this.PassportExpDate = this.userObj.PassportExpDate.setMonth(this.userObj.PassportExpDate.getMonth() - 6)

        if (this.userObj.IsDeviationRequest === true) {
          this.isDeviation = true;
        } else {
          this.isDeviation = false;
        }
        if (this.userObj.Travel_x0020_For === "Dependent") {
          this.isDependent = true;
        } else if (this.userObj.Travel_x0020_For === "Self along with Dependents") {

          this.isDependent = true;
        } else {
          this.isDependent = false;
        }
        if (this.userObj.TravelType === "International") {
          this.isShowDomestic = false;
          this.isShow = true;
        } else {
          this.isShow = false;
          this.isShowDomestic = true;
        }
        if (this.userObj.AdvanceRequired == true) {
          this.userObj.AdvanceRequired = "true";

          this.isAdvance = true
        } else {
          this.userObj.AdvanceRequired = "false";
        }
        if (this.userObj.AccommodationRequired == true) {
          this.userObj.AccommodationRequired = "true";
        } else {
          this.userObj.AccommodationRequired = "false";
        }
        if (this.userObj.ClientDebit == true) {
          this.userObj.ClientDebit = "true";
        } else {
          this.userObj.ClientDebit = "false";
        }
        if (this.userObject.AdvanceDetails.length > 0) {
          this.AdvanceDetails = this.userObject.AdvanceDetails;
          this.AdvanceDetails.forEach((element) => {
            if (element.Card == "0" || element.Card == 0) {
              element.carddisable = true;
            }
            if (element.Cash == "0" || element.Cash == 0) {
              element.cashdisable = true;
            }
          });
        } else {
          this.AdvanceDetails = this.AdvanceDetails;
        }
        this.rgid = this.userObj.ID;
        this.tripDeatilsArray = this.userObject.tripDeatilsArray;

        setTimeout(() => {
          this.tripDeatilsArray.forEach((element) => {
            element.Actions = "NoUpdate";
            element.ArrivalDate = new Date(element.ArrivalDate === null ? element.ActualDate : element.ArrivalDate);
            element.TravelDate = new Date(element.TravelDate);
            element.ActualDate = new Date(element.ActualDate);
            element.minDate = new Date(element.ActualDate);
            element.minTravelDate = new Date(element.ActualDate);
          });
        }, 2000);

        this.DependentAdd = this.userObject.DependentAdd;
        if (
          this.userObj.TripType === "RoundTrip" ||
          this.userObj.TripType === "OneWay"
        ) {
          this.userObj.FromCity = this.tripDeatilsArray[0].Form;
          this.userObj.ToCity = this.tripDeatilsArray[0].To;
        }
        if (this.userObj.TripType === "MultiCity") {
          this.userObj.FromCity = this.tripDeatilsArray[0].Form;
          this.userObj.ToCity = this.tripDeatilsArray[0].Form;
        }
        this.AdvanceDetails.forEach((element) => {
          element.Card = element.Card ? Number.parseFloat(element.Card) : 0;
          element.Cash = element.Cash ? Number.parseFloat(element.Cash) : 0;
        });

        this.isTableShow = true;
        this.informService
          .GetDependentsById(this.userObj.Title)
          .then((resp: any) => {
            this.DependentArrayView = resp;
            if (this.DependentArrayView && this.DependentArrayView.length > 0) {
              this.DependentArrayView.forEach((item) => {
                item.Selected = false;
                item.DOBPassport = new Date(item.DOBPassport);
                item.PassportExpiryDate = new Date(item.PassportExpiryDate);
              });
              this.DependentAdd.forEach((response: any) => {

                this.DependentArrayView.forEach((p: any) => {
                  this.isDependent = true

                  if (
                    response.GivenName == p.GivenName &&
                    response.SurName == p.SurName
                  ) {
                    p.Selected = true;
                    p.VisaValidTill = new Date(response.VisaValidTill);
                    p.VisaStatus = response.VisaStatus;
                    p.TypeofVisa = response.TypeofVisa;
                    p.VisaNo = response.VisaNumber;
                    p.RejectionReason = response.RejectionReason;
                  }
                });
              });
              this.firstDependentIndex = this.DependentArrayView.findIndex(item => item.Selected);
            }
          });
        if (this.userObj.TravelType === 'International') {

          if (this.userObj.Travel_x0020_For === "Self") {
            this.checkInterAssPass();
          } else if (this.userObj.Travel_x0020_For === "Dependent") {
            this.checkInterDepPass();
          } else if (this.userObj.Travel_x0020_For === "Self along with Dependents") {
            this.checkInterDepPass();
            this.checkInterAssPass();
          }

        }
        this.GetTickets();
          (this.userObj.ReportingDate = new Date(this.userObj.ReportingDate)),
          this.informService.getCostDetails(this.rgid).then((response: any) => {
            this.Costdetails = response;
            let AmountPaid: any = 0.0;
            this.Costdetails.forEach((element) => {
              element.TotalCost ? Number.parseFloat(element.TotalCost) : 0;
              AmountPaid =
                Number.parseFloat(AmountPaid) +
                Number.parseFloat(element.TotalCost);
            });
            this.userObj.Cost = AmountPaid;
          });
        this.informService.GetDepertName().then((res: any) => {
          this.Depart = res;
        });
     
        this.informService.GetBookingNames().then((res: any) => {
          this.BookingNames = res;
        });
        this.informService
          .GetProject(this.userObj.EmployeeID)
          .then((res: any) => {
            this.projectoaid = res;
            this.projectoaid.forEach((element) => {
              element.deploymentdate__c = new Date(element.deploymentdate__c);
              element.enddate__c = new Date(element.enddate__c);
            });
            let checkDate = new Date();
            for (var i = 0; i < this.BookingNames.length; i++) {
              var ele = this.BookingNames[i];
              for (var j = 0; j < this.projectoaid.length; j++) {
                var res = this.projectoaid[j];
                if (ele &&
                  ele.project_id == res.bookingprojectoaid__c &&
                  (ele.projectdepartmentname__c != null ||
                    ele.projectdepartmentname__c != undefined) &&
                  checkDate >= res.deploymentdate__c &&
                  checkDate <= res.enddate__c
                ) {
                  this.common.push(ele);
                }
              }
            }
            this.el = this.common.find(
              (e) => e.ProjectList == this.userObj.Project
            );
            if (this.el != undefined && (
              this.el.projectdepartmentname__c != undefined ||
              this.el.projectdepartmentname__c != null
            )) {
              this.Delivery = this.Depart.find(
                (eleme) =>
                  eleme.Department_name == this.el.projectdepartmentname__c
              );
            } else {
              this.Delivery = this.Depart.find(
                (eleme) => eleme.Department_name == this.userObj.Department
              );
            }
            if (
              this.Delivery.department_type_name != null ||
              this.Delivery.department_type_name != undefined
            ) {
              if (this.Delivery.department_type_name == "DELIVERY") {
                this.DeliverySource = this.informService.TravelCategory.filter(
                  (eleme) => eleme.department_type_name == "DELIVERY"
                );
                this.userObj.IsProjectTravel = true;
              } else {
                this.DeliverySource = this.informService.TravelCategory.filter(
                  (eleme) => eleme.department_type_name != "DELIVERY"
                );
                this.userObj.IsProjectTravel = false;
              }
            }
            setTimeout(() => {
              if (this.Hrinfo && this.Hrinfo.length > 0) {
                if (
                  this.RId != null &&
                  this.RId != undefined &&
                  this.Appkey === "Reschedule"
                ) {
                  let isDMavailable = false;
                  let isTPavailable = false;
                  let FMSuperior;
                  let FMSuperSuperior;
                  let Account;
                  if (
                    this.el.accountmanagerprismcode__c != null ||
                    this.el.accountmanagerprismcode__c != undefined
                  ) {
                    Account = this.Hrinfo.find(
                      (el) => this.el.accountmanagerprismcode__c == el.Title
                    );
                  }
                  let FM = this.Hrinfo.find(
                    (el) => this.informService.Current.EmployeeID == el.Title
                  );
                  if (FM != null || FM != undefined) {
                    FMSuperior = this.Hrinfo.find(
                      (el) => FM.functional_reporting_to == el.Title
                    );
                    FMSuperSuperior = this.Hrinfo.find(
                      (el) => FMSuperior.functional_reporting_to == el.Title
                    );
                    this.userObj.ApprovalAuthority = FM.maveric_email_id;
                  }
                  if (FMSuperior != null || FMSuperior != undefined) {
                    this.userObj.ApproversSuperior =
                      FMSuperior.maveric_email_id;
                  }

                  if (
                    this.el.deliverymanagerprismcode__c != null ||
                    this.el.deliverymanagerprismcode__c != undefined
                  ) {
                    this.DM = this.Hrinfo.find(
                      (el) => this.el.deliverymanagerprismcode__c == el.Title
                    );
                    if (this.DM != null || this.DM != undefined) {
                      this.DMSuperior = this.Hrinfo.find(
                        (el) => this.DM.functional_reporting_to == el.Title
                      );
                    }
                    isDMavailable = true;
                  }
                  if (
                    this.el.technicalpartnerprismcode__c != null ||
                    this.el.technicalpartnerprismcode__c != undefined
                  ) {
                    this.TP = this.Hrinfo.find(
                      (el) => this.el.technicalpartnerprismcode__c == el.Title
                    );
                    if (this.TP != null || this.TP != undefined) {
                      this.TPSuperior = this.Hrinfo.find(
                        (el) => this.TP.functional_reporting_to == el.Title
                      );
                    }
                    isTPavailable = true;
                  }

                  if (isDMavailable && isTPavailable) {
                    if (
                      this.DM.maveric_email_id ==
                      this.informService.Current.MailID
                    ) {
                      this.EditApprovalAuthority = this.TP.maveric_email_id;
                      this.EditApproversSuperior = this.TPSuperior.maveric_email_id;
                      this.userObj.ApprovalAuthority = this.EditApprovalAuthority;
                      this.userObj.ApproversSuperior = this.EditApproversSuperior;
                    } else if (
                      this.TP.maveric_email_id ==
                      this.informService.Current.MailID
                    ) {
                      if (
                        this.TPSuperior != null ||
                        this.TPSuperior != undefined
                      ) {
                        this.TPSuperSuperior = this.Hrinfo.find(
                          (el) =>
                            this.TPSuperior.functional_reporting_to == el.Title
                        );
                      }
                      this.EditApprovalAuthority = this.TPSuperior.maveric_email_id;
                      this.EditApproversSuperior = this.TPSuperSuperior.maveric_email_id;
                      this.userObj.ApprovalAuthority = this.EditApprovalAuthority;
                      this.userObj.ApproversSuperior = this.EditApproversSuperior;
                    } else {
                      this.EditApprovalAuthority = this.DM.maveric_email_id;
                      this.EditApproversSuperior = this.TP.maveric_email_id;
                      this.userObj.ApprovalAuthority = this.EditApprovalAuthority;
                      this.userObj.ApproversSuperior = this.EditApproversSuperior;
                    }
                  } else if (!isDMavailable && isTPavailable) {
                    if (
                      this.TP.maveric_email_id ==
                      this.informService.Current.MailID
                    ) {
                      if (
                        this.TPSuperior != null ||
                        this.TPSuperior != undefined
                      ) {
                        this.TPSuperSuperior = this.Hrinfo.find(
                          (el) =>
                            this.TPSuperior.functional_reporting_to == el.Title
                        );
                      }
                      this.EditApprovalAuthority = this.TPSuperior.maveric_email_id;
                      this.EditApproversSuperior = this.TPSuperSuperior.maveric_email_id;
                      this.userObj.ApprovalAuthority = this.EditApprovalAuthority;
                      this.userObj.ApproversSuperior = this.EditApproversSuperior;
                    } else {
                      this.EditApprovalAuthority = this.TP.maveric_email_id;
                      this.EditApproversSuperior = this.TPSuperior.maveric_email_id;
                      this.userObj.ApprovalAuthority = this.EditApprovalAuthority;
                      this.userObj.ApproversSuperior = this.EditApproversSuperior;
                    }
                  } else if (isDMavailable && !isTPavailable) {
                    if (
                      this.DM.maveric_email_id ==
                      this.informService.Current.MailID
                    ) {
                      this.EditApprovalAuthority = this.DMSuperior.maveric_email_id;
                      this.userObj.ApprovalAuthority = this.EditApprovalAuthority;
                      this.DMSuperiorSuper = this.Hrinfo.find(
                        (el) =>
                          this.DMSuperior.functional_reporting_to == el.Title
                      );
                      this.EditApproversSuperior = this.DMSuperiorSuper.maveric_email_id;
                      this.userObj.ApproversSuperior = this.EditApproversSuperior;
                    } else {
                      this.EditApprovalAuthority = this.DM.maveric_email_id;
                      this.EditApproversSuperior = this.DMSuperior.maveric_email_id;
                      this.userObj.ApprovalAuthority = this.EditApprovalAuthority;
                      this.userObj.ApproversSuperior = this.EditApproversSuperior;
                    }
                  } else if (!isDMavailable && !isTPavailable) {
                    this.EditApprovalAuthority = FMSuperior.maveric_email_id;
                    this.EditApproversSuperior =
                      FMSuperSuperior.maveric_email_id;
                    this.userObj.ApprovalAuthority = this.EditApprovalAuthority;
                    this.userObj.ApproversSuperior = this.EditApproversSuperior;
                  } else {
                    this.EditApprovalAuthority = FMSuperior.maveric_email_id;
                    this.EditApproversSuperior =
                      FMSuperSuperior.maveric_email_id;
                    this.userObj.ApprovalAuthority = this.EditApprovalAuthority;
                    this.userObj.ApproversSuperior = this.EditApproversSuperior;
                  }
                  if (
                    this.EditApprovalAuthority == this.EditApproversSuperior ||
                    this.informService.Current.MailID ==
                    this.EditApprovalAuthority ||
                    this.informService.Current.MailID ==
                    this.EditApproversSuperior
                  ) {
                    this.EditApprovalAuthority = "";
                    this.EditApproversSuperior = "";
                    this.userObj.ApprovalAuthority = this.EditApprovalAuthority;
                    this.userObj.ApproversSuperior = this.EditApproversSuperior;
                    this.toaster.errorToastr(
                      "unable to fetch approval details please contact administrator"
                    );
                  }
                }
              }
            }, 5000);
          });
      });
    }
  }

  showMoreInfo(template: TemplateRef<any>, index, isUserObj) {
    isUserObj ? this.selectedDependantIndex = -1 :
      this.selectedDependantIndex = index;
    this.bsModalRef = this.modalService.show(template, this.config);
  }

  GetDependentsList() {
    this.informService.GetDependents().then((res: any) => {
      this.DependentArrayView = res;
      if (this.DependentArrayView && this.DependentArrayView.length > 0) {
        this.DependentArrayView.forEach((item) => {
          item.Selected = false;
          item.DOBPassport = new Date(item.DOBPassport);
          item.PassportExpiryDate = new Date(item.PassportExpiryDate);

        });
      }
    });
  }

  CalculateAge(d: Date) {
    return new Date().getFullYear() - new Date(d).getFullYear();
  }
  
  checkInterAssPass() {
    for (var i = 0; i < this.tripDeatilsArray.length; i++) {
      if (this.tripDeatilsArray[i].TravelDate != null && this.tripDeatilsArray[i].TravelDate != "") {
        this.tripDeatilsArray[i].TravelDate = new Date(this.tripDeatilsArray[i].TravelDate);
        if (this.userObj.PassportExpiryDate < this.tripDeatilsArray[i].TravelDate) {
          this.tripDeatilsArray[i].isPasswordExpireInSixMnt = false;
          this.toaster.errorToastr("Travel Date should be on (or) before passport expiry date");
          setTimeout(() => {
            this.tripDeatilsArray[i].TravelDate = null;
            if (this.userObj.Travel_x0020_For === "Self along with Dependents") {
              this.checkInterDepPass();
            }
          }, 10);
          break
        } else {
          if (this.tripDeatilsArray[i].TravelDate >= this.PassportExpDate) {
            this.tripDeatilsArray[i].isPasswordExpireInSixMnt = true;
          }
          else {
            this.tripDeatilsArray[i].isPasswordExpireInSixMnt = false;
          }
        }

      }
      else {
        this.tripDeatilsArray[i].isPasswordExpireInSixMnt = false

      }
      const data = this.tripDeatilsArray.filter(x => x.isPasswordExpireInSixMnt === true);
      this.isPasswordExpireInSixMnt = data.length ? true : false;
    }
  }

  checkInterDepPass() {
    for (var j = 0; j < this.DependentAdd.length; j++) {
      var ele = this.DependentAdd[j];
      ele.PassportExpiryDate = new Date(ele.PassportExpiryDate)

      for (var k = 0; k < this.tripDeatilsArray.length; k++) {

        if (this.tripDeatilsArray[k].TravelDate != null && this.tripDeatilsArray[k].TravelDate != "" && this.tripDeatilsArray != undefined) {
          this.tripDeatilsArray[k].TravelDate = new Date(this.tripDeatilsArray[k].TravelDate);
          if (ele.PassportExpiryDate < this.tripDeatilsArray[k].TravelDate) {
            this.tripDeatilsArray[k].DependSixMnt = false
            this.toaster.errorToastr(" Travel Date should be on (or) before dependent passport expiry date");
            for (var d = k; k < this.tripDeatilsArray.length; k++) {
              this.tripDeatilsArray[d].TravelDate = null;
              this.tripDeatilsArray[d].DependSixMnt = false
            }
          }
          else {
            if (this.tripDeatilsArray[k].TravelDate >= ele.DepndPassportExpDate) {
              this.tripDeatilsArray[k].DependSixMnt = true
            }
          }
        }
        else {
          this.tripDeatilsArray[k].DependSixMnt = false

        }

        const data = this.tripDeatilsArray.filter(x => x.DependSixMnt === true);
        this.DependSixMnt = data.length ? true : false;
      }
    }
  }
  
  GetTickets() {
    this.informService.GetTravelTicket(this.rgid).then((res: any) => {
      this.TicketFiles = res;
      this.TicketFiles.forEach((element) => {
        let temp: any = {};
        temp.FileName = element.FileName;
        temp.CreatedTime = element.CreatedTime;
        temp.CanDisplay = false;
        temp.CanRemove = false;
        // this.canAttach.push(temp);
      });
      this.filesData = JSON.parse(this.userObject.TravelRequest.FileName);
    
      for (let index = 0; index < this.TicketFiles.length; index++) {
        const ele = this.TicketFiles[index];
        if (this.filesData && this.filesData.length > 0) {
          if (
            this.filesData.findIndex((e) => e.FileName === ele.FileName) > -1
          ) {
            ele.CanDisplay = true;
            ele.CanRemove = false;
          } else {
            ele.CanDisplay = true;
            ele.CanRemove = true;
          }
        } else {
          ele.CanDisplay = true;
          ele.CanRemove = true;
        }
      }
    });
  }

  CancelBack() {
    this.router.navigate(["/viewticket"]);
  }

  download(file, name) {
    saveAs(`https://mavericsystems.sharepoint.com/sites/app_data_center/_layouts/Download.aspx?sourceurl=${file}`, name);
  }
}
