import { Component, OnInit } from '@angular/core';
import { InfoService } from '../shared/info.service';
import { ActivatedRoute, Router } from '@angular/router';
import { element } from '@angular/core/src/render3';

@Component({
  selector: 'app-view-travel-request',
  templateUrl: './view-travel-request.component.html',
  styleUrls: ['./view-travel-request.component.css']
})
export class ViewTravelRequestComponent implements OnInit {
  dtOptions: DataTables.Settings = {};
  Id: any;
  FullDetails: any[] = [];
  Modified: any[] = [];
  constructor(
    private informService: InfoService,
    private activatedRoute: ActivatedRoute,
    private router: Router
  ) {
    if (this.activatedRoute.snapshot.params["id"]) {
      this.Id = +atob(this.activatedRoute.snapshot.params["id"]);
    }
  }
  ngOnInit() {
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 10,
      paging: true,
      searching: true,
      info: true,
      ordering: false
    };
    if (this.Id != null && this.Id > 0) {
      this.informService.GetAllVersions(this.Id).then((res: any) => {
        this.FullDetails = res;
        // for (let index = res.length - 1; index > 0; index--) {
        //   //const element = res[index];
        //   //if (this.FullDetails[index].Form) {
        //     if (res[index].Form == res[index - 1].Form) {
        //       res[index - 1].Form = "";
        //     }
        //  // }
        // }
      });
    }
  }
  BackToNavigate() {
    this.router.navigate(['/UpdateTicket']);
  }
  CheckItemChange(key, index) {
    if (index>=this.FullDetails.length-1) {
      return true;
    }
    if (this.FullDetails[index][key]==this.FullDetails[index+1][key]) {
      return false;
    }else{
    return true;
    }
  }

}
