import { Component, OnInit, TemplateRef } from '@angular/core';
import { InfoService } from '../shared/info.service';
import { Router } from '@angular/router';
import { ToastrManager } from 'ng6-toastr-notifications';
import { cloneDeep } from 'lodash';

@Component({
  selector: 'app-visa-process',
  templateUrl: './visa-process.component.html',
  styleUrls: ['./visa-process.component.css']
})
export class VisaProcessComponent implements OnInit {

  dtOptions: DataTables.Settings = {};
  GetmyTravel: any[] = [];
  MyApproval: any[] = [];
  Odata: any;
  userObj: any = {};
  pageShow = false;
  configsm = {
    backdrop: true,
    class: 'modal-xl',
    ignoreBackdropClick: true
  };
  CommentId: any;
  comments: string = ''
  GetmyTravelById: any = {};
  GetTravelProfileInitial: any[] = [];
  datatable: boolean = false;
  datatable1: boolean = false;
  ProxyEmail: any;
  ProxyName: any;
  TravelStatus: any;
  Route: any[] = [];
  isVisaSubmitted: boolean;
  today = new Date();
  filteredData: any[] = [];
  filterKey: string = 'Visa Pending';
  filterDropdown: any[] = [
    { value: "Travel Approved", displayName: "Travel Approved" },
    { value: "Booked", displayName: "Booked" },
    { value: "Reschedule Approved", displayName: "Reschedule Approved" },
    { value: "Rescheduled", displayName: "Rescheduled" },
    { value: "Reschedule Requested", displayName: "Rescheduled Requested" },
    { value: "Reschedule Required", displayName: "Reschedule Required" },
    { value: "Cancellation Requested", displayName: "Cancellation Requested" },
    { value: "Cancelled", displayName: "Cancelled" },
    { value: "Visa Pending", displayName: "Visa Pending" },
    { value: "Visa Rejected", displayName: "Visa Rejected" },
    { value: "Void", displayName: "Void" },
    { value: "All", displayName: "All" }
  ];
  searchKey: string = '';


  constructor(public infoService: InfoService,
    private router: Router,
    private toaster: ToastrManager,) {
    this.ProxyEmail = this.infoService.getLocalStorage('ProxyEmail');
    if (this.ProxyEmail != null || this.ProxyEmail != undefined) {
      this.ProxyName = this.ProxyEmail;
    }
  }

  ngOnInit() {
    this.userObj = this.infoService.Current;
    this.infoService.GetCurrentUser(() => {
      this.infoService.GetTravelProfileDetails().then((res: any) => {
        this.GetTravelProfileInitial = res;
        if (this.GetTravelProfileInitial.length > 0) {
        } else {
          this.router.navigate(['/MyProfile']);
          this.toaster.successToastr('Please update your profile');
        }
      });
      if (this.ProxyName != null || this.ProxyName != undefined) {
        this.infoService.GetVisaSubmittedByProxy(this.ProxyName).then((resp: any) => {
          this.GetmyTravel = resp;
          if (this.GetmyTravel.length > 0) {
            this.datatable1 = false;
            this.filteringData();
          }
          if (this.GetmyTravel.length == 0) {
            this.datatable1 = true;
          }

        });
      } else {
        this.infoService.GetVisaSubmittedList().then((response: any) => {
          this.GetmyTravel = response;
          if (this.GetmyTravel.length > 0) {
            this.datatable1 = false;
            this.filteringData();
          }
          if (this.GetmyTravel.length == 0) {
            this.datatable1 = true;
          }

        });
      }
    });
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 10,
      paging: true,
      searching: false,
      // info: true,
      ordering: false
    };
  }

  visaStatus(eve: any, item) {
    if (eve.value === "Submitted" || eve.value === "On-arrival" || eve.value === "Return to home country") {
      if (eve.value === "On-arrival" || eve.value === "Return to home country") {
        item.TypeofVisa = eve.value === "On-arrival" ? "On Arrival Visa" : "Return to home country visa";
      }
      this.isVisaSubmitted = true;
      item.VisaValidTill = null;
      item.VisaNo = "";
    }
    else {
      this.isVisaSubmitted = false;
    }
  }


  edit(key, id) {
    this.router.navigate(['/NewTravelRequest', key, id]);
  }

  filteringData() {
    this.searchKey = "";
    if (this.filterKey == 'All') {
      this.filteredData = cloneDeep(this.GetmyTravel);
    } else {
      this.filteredData = cloneDeep(this.GetmyTravel.filter(x => x.TravelStatus == this.filterKey));
    }
  }

  searchKeyChanged() {
    if (this.searchKey && this.searchKey.trim().length > 0) {
      this.filteredData = this.filteredData.filter(s =>
        (s.Id && (s.Id.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.EmployeeName && (s.EmployeeName.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.EmployeeID && (s.EmployeeID.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.Project && (s.Project.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.VisaStatus && (s.VisaStatus.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.VisaValidTill && (s.VisaValidTill.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.VisaNo && (s.VisaNo.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.TypeofVisa && (s.TypeofVisa.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.Country && (s.Country.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.Region && (s.Region.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.TravelStatus && (s.TravelStatus.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0))
      );
    } else {
      this.filteringData();
    }
  }

}
