  GetCurrentUser(cb?) {
    this.ProxyEmail = this.getLocalStorage("ProxyEmail");
    pnp.sp.web.currentUser.get().then((r) => {
      pnp.sp.web.lists
        .getByTitle("hr_employee_info")
        .items.select(
          "Title,GUID,OU,functional_reporting_to,full_name,department,designation,business_unit,cluster,base_location,gender,dob,maveric_email_id,cost_center"
        )
        // .filter(`substringof('${name}',maveric_email_id)`)
        .filter("maveric_email_id eq '" + r.Email + "'")
        .top(1)
        .get()
        .then((result) => {
          this.actualUserId = result[0].Title;
          return pnp.sp.web.siteUsers
          .getByEmail(result[0].maveric_email_id)
          .groups.get()
          .then((groupsData) => {
            const isAdmin = groupsData.filter((obj) => obj.Title === "Admins");
              this.isAdmin = isAdmin.length > 0 ? true: false; 
          });
        });
      let name = r.UserPrincipalName;
      // this.SetUserType(r.UserPrincipalName);
      if (this.ProxyEmail != null || this.ProxyEmail != undefined) {
        name = this.ProxyEmail;
      }
      // name = "balajip@maveric-systems.com";
      // name = "abhinandna@maveric-systems.com";
      // name = "ashokkumart@maveric-systems.com";
      this.SetUserType(name);
      this.getComments();
      return (
        pnp.sp.web.lists
          .getByTitle("hr_employee_info")
          .items.select(
            "Title,GUID,OU,functional_reporting_to,full_name,department,designation,business_unit,cluster,base_location,gender,dob,maveric_email_id,cost_center"
          )
          // .filter(`substringof('${name}',maveric_email_id)`)
          .filter("maveric_email_id eq '" + name + "'")
          .top(1)
          .get()
          .then((res) => {
            if (res.length > 0) {
              this.Current.EmployeeID = res[0].Title;
              this.Current.EmployeeName = res[0].full_name;
              this.Current.Department = res[0].department;
              this.Current.Designation = res[0].designation;
              this.Current.FullBusinessUnit = res[0].business_unit;
              this.Current.FullDeliveryUnit = res[0].cluster;
              if (res[0].OU) {
                const units: any = res[0].OU.split("-");
                if (units && units.length > 0) {
                  this.Current.DeliveryUnit = last(units);
                  this.Current.BusinessUnit = units.length > 1 ? units[units.length - 2] : "";
                }
              }
              // this.Current.BusinessUnit = res[0].business_unit;
              // this.Current.DeliveryUnit = res[0].OU;
              this.Current.BaseLocation = res[0].base_location;
              this.Current.Gender = res[0].gender;
              this.Current.Age = this.CalculateAge(res[0].dob);
              this.Current.DOB = new Date(res[0].dob);
              this.Current.MailID = res[0].maveric_email_id;
              this.Current.functional_reporting_to =
                res[0].functional_reporting_to;
              this.Current.CostCenter = res[0].cost_center;
              this.Current.OU = res[0].OU;

              // this.GetTravelProfileDetails();

              if (cb) cb(this.Current);
            } else {
              // alert("User Profile not availabe")
            }
          })
      );
    });
    //return this.CurrentUser;
  }

  getComments() {
    return pnp.sp.web.lists
      .getByTitle("Comments")
      .items.select(
        "Comments", "TypeName"
      )
      .filter("Is_Active eq 1")
      .getAll()
      .then((res) => {
        this.DeviationReason = res.filter((data) => data.TypeName === "Deviation").map((item) => item.Comments);
        this.CancelArrayDelivery = res.filter((data) => data.TypeName === "ProjectTravelCancellation").map((item) => item.Comments);
        this.CancelArrayNonDelivery = res.filter((data) => data.TypeName === "NonProjectTravelCancellation").map((item) => item.Comments);
        this.commentArray = res.filter((data) => data.TypeName === "ProjectTravelRejection").map((item) => item.Comments);
        this.commentsnon = res.filter((data) => data.TypeName === "NonProjectTravelRejection").map((item) => item.Comments);
        this.RescheduleArray = res.filter((data) => data.TypeName === "INProjectTravelReschedule").map((item) => item.Comments);
        this.RescheduleNonArray = res.filter((data) => data.TypeName === "INNonProjectTravelReschedule").map((item) => item.Comments);
        this.DomesticRescheduleArray = res.filter((data) => data.TypeName === "DomProjectTravelReschedule").map((item) => item.Comments);
        this.DomesticRescheduleNonArray = res.filter((data) => data.TypeName === "DomNonProjectTravelReschedule").map((item) => item.Comments);
        this.visaRejectionComments = res.filter((data) => data.TypeName === "VisaRejection").map((item) => item.Comments);
      });
  }

