  GetTravelRequestFull(isViewTicket): any {
    return pnp.sp.web.lists
      .getByTitle("TravelRequest")
      .items.select(
        "id,Title,GUID,ReportingDate,EmployeeID,EmployeeName,TravelPurpose,TravelType,TravelStatus,Project,projectoraclecode__c"
      )
      .getAll()
      .then((res) => {

        let data = _.reverse(res);
        if (isViewTicket) {
          data = data.filter(item => item.TravelStatus === "Travel Approved" || item.TravelStatus === "Booked" || item.TravelStatus === "Reschedule Approved" || item.TravelStatus === "Rescheduled" || item.TravelStatus === "Cancellation Requested" || item.TravelStatus === "Cancelled");
        }
        return data && data.length > 0 ? data : [];
      });
  }
