  getDependentTravelByTicketId() {
    return pnp.sp.web.lists
      .getByTitle("TravelDependents")
      .items.select(
        "id,TravelTicketNoId,Title,GivenName,SurName,PassportNo,DOBPassport,PassportExpiryDate,Gender,Relationship"
      )
      .get()
      .then((dependentArray: any[]) => {
        this.travelRequestDetails.forEach(el => {
          const newarr = dependentArray.filter(x => x.TravelTicketNoId === el.Id)
          if (newarr.length) {
            this.filteredDependentArray.push(newarr)
          }

        })
        for (let i = 0; i < this.filteredDependentArray.length; i++) {
          this.filteredDependentArray[i].forEach((el, index) => {
            this.travelRequestDetails.forEach(element => {
              if (element.Id == el.TravelTicketNoId) {
                element[`Name ${index + 1}`] = el.GivenName
                element[`SurName ${index + 1}`] = el.SurName
                element[`Gender ${index + 1}`] = el.Gender
                element[`Relationship ${index + 1}`] = el.Relationship
              }
            });
          });
        }
      })
  }
