  TravelRequestBookedUpdate(Travelobj: any, RouteObj: any): any {
    var Id = Travelobj.Id;
    let Idr = RouteObj.Id;
    RouteObj.TicketStatus = "Booked";
    return pnp.sp.web.lists
      .getByTitle("Route")
      .items.getById(Idr)
      .update(RouteObj)
      .then((result) => {
        this.CostResponse = result;
        pnp.sp.web.lists
          .getByTitle("Route")
          .items.select(
            "id,Title,TravelTicketNoId,TicketStatus,Form,To,TravelDate,FirstPreferance,SecondPreferance,ActualDate,ArrivalDate"
          )
          .filter("TravelTicketNoId eq " + Id)
          .getAll()
          .then((res: any) => {
            this.GetArray = res;
            Travelobj.TicketStatus = "Open";
            if (
              this.GetArray.filter((e) => e.TicketStatus == "Cancelled")
                .length == this.GetArray.length
            ) {
              Travelobj.TravelStatus = "Cancelled";
              Travelobj.TicketStatus = "Completed";
            } else if (
              this.GetArray.filter((e) => e.TicketStatus == "Rescheduled")
                .length == this.GetArray.length
            ) {
              Travelobj.TravelStatus = "Rescheduled";
              Travelobj.TicketStatus = "Completed";
            } else if (
              this.GetArray.filter((e) => e.TicketStatus == "Rescheduled")
                .length > 0
            ) {
              Travelobj.TravelStatus = "Rescheduled";
              Travelobj.TicketStatus = "Completed";
            } else if (
              this.GetArray.filter((e) => e.TicketStatus == "Open").length ==
              0 &&
              (this.GetArray.filter((e) => e.TicketStatus == "Cancel Requested")
                .length > 0 ||
                this.GetArray.filter(
                  (e) => e.TicketStatus == "Reschedule Requested"
                ).length > 0)
            ) {
              Travelobj.TravelStatus = "Booked";
              Travelobj.TicketStatus = "Completed";
            } else if (
              this.GetArray.filter((e) => e.TicketStatus == "Open").length == 0
            ) {
              Travelobj.TravelStatus = "Booked";
              Travelobj.TicketStatus = "Completed";
            } else if (
              this.GetArray.filter((e) => e.TicketStatus == "Open").length > 0
            ) {
              // Travelobj.TravelStatus = "Travel Approved";
              Travelobj.TicketStatus = "Partially Booked";
            }
            pnp.sp.web.lists
              .getByTitle("TravelRequest")
              .items.getById(Id)
              .update(Travelobj)
              .then((response) => {
                return response;
              });
            return res;
          });
        return result;
      });
  }
