  SetUserType(mailid): any {
    return pnp.sp.web.siteUsers
      .getByEmail(mailid)
      .groups.get()
      .then((groupsData) => {
        var IsAdmin = groupsData.filter((obj) => {
          return obj.Title === "Admins";
        });
        var IsTravelDesk = groupsData.filter((obj) => {
          return obj.Title === "Travel Desk";
        });
        var IsSupportTeam = groupsData.filter((obj) => {
          return obj.Title === "Support Team";
        });
        var IsFinanceTeam = groupsData.filter((obj) => {
          return obj.Title === "Travel Finance View";
        });
        // var IsVisaTeam = groupsData.filter((obj) => {
        //   return obj.Title === "Travel Finance View";
        // });
        var IsSuperAdminTeam = groupsData.filter((obj) => {
          return obj.Title === "Super Admin Access Travel Application";
        });
        if (IsAdmin.length > 0) {
          this.UserType = "Admin";
        } else if (IsTravelDesk.length > 0) {
          this.UserType = "Travel Desk";
        } else if (IsSupportTeam.length > 0) {
          this.UserType = "Support Team";
        } else if (IsFinanceTeam.length > 0) {
          this.UserType = "Finance Team";
        }
        //  else if (IsVisaTeam.length > 0) {
        //   this.UserType = "Visa Team";
        // } 
        else if (IsSuperAdminTeam.length > 0) {
          this.UserType = "Super Admin";
        } else {
          this.UserType = "EmployeeUser";
        }

        this.isFinanceUserType = IsFinanceTeam.length > 0 ? true : false;
        // this.isVisaUserType = IsVisaTeam.length > 0 ? true : false;
        this.IsSuperAdminUserType = IsSuperAdminTeam.length > 0 ? true : false;
        this.visaOfficerLocations = [];
        // if (this.isVisaUserType || this.IsSuperAdminUserType) {
        pnp.sp.web.lists
          .getByTitle("VisaOfficerLocation")
          .items.select(
            "ID,country_id,country,region,region_id,officer_employee_id,officer_mail_id,officer_name,location_id,location,created_on,created_by,modified_on,modified_by"
          )
          .filter(`substringof('${mailid}',officer_mail_id)  and is_active eq 1`)
          .getAll()
          .then((res) => {
            this.visaOfficerLocations = res;
          });
        // } else {
        //   this.visaOfficerLocations = [];
        // }
      });
  }
