import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { orderBy } from 'lodash';
import { ToastrManager } from 'ng6-toastr-notifications';
import { InfoService } from '../shared/info.service';

@Component({
  selector: 'app-feedback-restriction',
  templateUrl: './feedback-restriction.component.html',
  styleUrls: ['./feedback-restriction.component.css']
})
export class FeedbackRestrictionComponent implements OnInit {

  dtOptions: DataTables.Settings = {};
  sourceData: any[] = [];
  filteredData: any[] = [];
  filterKey: string = 'Travel Approved';
  searchKey: string = '';
  filterDropdown: any[] = [
    { value: "Travel Approved", displayName: "Travel Approved" },
    { value: "Booked", displayName: "Booked" },
    { value: "Reschedule Approved", displayName: "Reschedule Approved" },
    { value: "Rescheduled", displayName: "Rescheduled" },
    { value: "Cancellation Requested", displayName: "Cancellation Requested" },
    { value: "Cancelled", displayName: "Cancelled" },
    { value: "All", displayName: "All" }
  ];
  itemsAsObjects = [{ value: 1, display: 'Angular' }, { value: 2, display: 'React' }, { value: 3, display: 'Vue' }, { value: 4, display: 'Flask' }];
  itemsAsObjectsValues = [];
  onlyFromAutocomplete = false;
  displayItem = "display";
  identifyBy = "value";
  placeholder = "Add users"
  constructor(
    private infoService: InfoService,
    private router: Router,
    private toaster: ToastrManager,
  ) {
  }

  ngOnInit() {
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 10,
      paging: false,
      searching: false,
      info: false,
      ordering: false
    };

    this.infoService.GetCurrentUser(() => {
      this.infoService.GetTravelProfileDetails().then((response: any) => {
        if (!(response && response.length > 0)) {
          this.router.navigate(['/MyProfile']);
          this.toaster.successToastr('Please update your profile');
        }
      });
      this.getData();
    });
  }

  add() {
    if (this.itemsAsObjectsValues.length > 0) {
      let count = 0;
      this.itemsAsObjectsValues.forEach((item) => {
        this.infoService.updateRestriction(item.value.ID, true).then((res) => {
          count++;
          if (count === this.itemsAsObjectsValues.length) {
            this.itemsAsObjectsValues = [];
            this.getData();
          }
        });
      });
    }
  }

  remove(id) {
    this.infoService.updateRestriction(id, false).then(() => {        
        this.getData();
    });
  }

  getData() {
    this.infoService.GetFeedbackRestricted().then((result: any) => {
      if (result && result.length > 0) {
        this.sourceData = result;
        this.sourceData = orderBy(this.sourceData, "full_name", "asc");
      }
    });

    this.infoService.GetFeedbackNonRestricted().then((result: any) => {
      if (result && result.length > 0) {
        this.filteredData = result;
        this.filteredData.map((item) => { item.display = item.full_name, item.value = item })
      }
    });
  }

}
