import { Component, OnInit, TemplateRef } from '@angular/core';
import { InfoService } from '../shared/info.service';
import { Router } from '@angular/router';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { BsDatepickerConfig } from 'ngx-bootstrap/datepicker';
import { ToastrManager } from 'ng6-toastr-notifications';

@Component({
  selector: 'app-travel-request',
  templateUrl: './travel-request.component.html',
  styleUrls: ['./travel-request.component.css']
})
export class TravelRequestComponent implements OnInit {
  dtOptions: DataTables.Settings = {};
  bsConfig: Partial<BsDatepickerConfig> = { dateInputFormat: this.informService.DateFormate, adaptivePosition: true, showWeekNumbers: false };
  GetmyTravel: any[] = [];
  MyApproval: any[] = [];
  Odata: any;
  userObj: any = {};
  pageShow = false;
  configsm = {
    backdrop: true,
    class: 'modal-xl',
    ignoreBackdropClick: true
  };
  bsModalRef: BsModalRef;
  CommentId: any;
  comments: string = ''
  GetmyTravelById: any = {};
  GetTravelProfileInitial: any[] = [];
  datatable: boolean = false;
  datatable1: boolean = false;
  ProxyEmail: any;
  ProxyName: any;
  TravelStatus: any;
  Route: any[] = [];
  previousStatus: any = "";

  constructor(public informService: InfoService,
    private router: Router,
    private toaster: ToastrManager,
    private modalService: BsModalService,) {
    // this.informService.GetCurrentUser(() => {
    // });
    this.ProxyEmail = this.informService.getLocalStorage('ProxyEmail');
    if (this.ProxyEmail != null || this.ProxyEmail != undefined) {
      this.ProxyName = this.ProxyEmail;
    }
  }

  ngOnInit() {
    this.userObj = this.informService.Current;
    this.informService.GetCurrentUser(() => {
      this.informService.GetTravelProfileDetails().then((res: any) => {
        this.GetTravelProfileInitial = res;
        if (this.GetTravelProfileInitial.length > 0) {
          // this.router.navigate(['/']);
        } else {
          this.router.navigate(['/MyProfile']);
          this.toaster.successToastr('Please update your profile');
        }
      });
      if (this.ProxyName != null || this.ProxyName != undefined) {
        this.informService.GetMyTravelDetailsProxy(this.ProxyName).then((resp: any) => {
          this.GetmyTravel = resp;
          if (this.GetmyTravel.length > 0) {
            this.datatable1 = false;
          }
          if (this.GetmyTravel.length == 0) {
            this.datatable1 = true;
          }

        });
      } else {
        this.informService.GetMyTravelDetails().then((response: any) => {
          this.GetmyTravel = response;
          if (this.GetmyTravel.length > 0) {
            this.datatable1 = false;
          }
          if (this.GetmyTravel.length == 0) {
            this.datatable1 = true;
          }

        });
      }
    });
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 10,
      paging: true,
      searching: true,
      info: true,
      ordering: false
    };

    // this.informService.GetMyTravelDetails().then((res: any) => {
    //   this.GetmyTravel = res;
    // });
    // this.informService.GetMyApprovalDetails().then((temp: any) => {
    //   this.MyApproval = temp;
    // });
  }
  // BackToNewTravelRequest() {
  //   this.router.navigate(['/NewTravelRequest']);
  // }

  ToViewDetails(key: any, id: any) {
    // let temp = btoa(Id);
    this.router.navigate(['/NewTravelRequest', key, id]);
  }
  TravelEdit(key: any, id: any) {
    this.router.navigate(['/EditTravelRequest', key, id]);

  }
  TravelReschedule(key: any, id: any) {
    // let temp = btoa(Id);
    this.router.navigate(['/NewTravelRequest', key, id]);
  }
  TravelCancelUpdate(CancelPopup: TemplateRef<any>, x: any) {
    this.CommentId = x.Id;
    this.previousStatus = x.TravelStatus;
    this.TravelStatus = x.TravelStatus;
    this.informService.GetMyTravelDetailsById(this.CommentId).then((res: any) => {
      this.GetmyTravelById = res;
    });
    this.bsModalRef = this.modalService.show(CancelPopup, this.configsm);

  }

  TravelRequestCancellingApprove() {
    if (this.comments && this.comments != '') {
      if (this.TravelStatus == 'Pending Approval') {
        let obj = {
          Id: this.CommentId,
          CancellationComments: this.comments,
          TravelStatus: 'Cancelled'
        }

        const trackerData: any = {
          ActualUserEmpID: this.informService.actualUserId,
          LoggedInUserEmpID: this.informService.Current.EmployeeID,
          TravelTicketNo: this.CommentId,
          PreviousStatus: this.previousStatus,
          ActivityPerformed: "Cancelled",
          RejectionReason: this.comments,
          IsSameUserLoggedIn: this.informService.actualUserId == this.informService.Current.EmployeeID ? true : false
        };

        this.informService.CancelRequested(obj, trackerData).then((res: any) => {
          if (res) {
            this.bsModalRef.hide();
            this.ngOnInit();
            this.comments = '';
            this.router.navigate(['/']);
            this.toaster.successToastr('Travel is Cancelled');
          }
        })
      } else {
        let Id = this.CommentId;
        this.informService.GetRouteCancel(Id).then((res: any) => {
          this.Route = res;
          if (this.Route.length > 0) {
            this.Route.forEach(element => {
              if (element.TicketStatus == 'Cancelled') {

              } else {
                element.TicketStatus = 'Cancel Requested'
              }
            });
            let obj = {
              Id: this.CommentId,
              CancellationComments: this.comments,
              TravelStatus: 'Cancellation Requested',
              Route: this.Route
            }

            const trackerData: any = {
              ActualUserEmpID: this.informService.actualUserId,
              LoggedInUserEmpID: this.informService.Current.EmployeeID,
              TravelTicketNo: this.CommentId,
              PreviousStatus: this.previousStatus,
              ActivityPerformed: "CancellationRequested",
              RejectionReason: this.comments,
              IsSameUserLoggedIn: this.informService.actualUserId == this.informService.Current.EmployeeID ? true : false
            };

            this.informService.CancelRequestedRoute(obj, trackerData).then((res: any) => {
              if (res) {
                this.bsModalRef.hide();
                this.ngOnInit();
                this.comments = '';
                this.router.navigate(['/']);
                this.toaster.successToastr('Cancellation requested');
              }
            })
          }
          //  else {
          //   let obj = {
          //     Id: this.CommentId,
          //     Remarks: this.comments,
          //     TravelStatus: 'Cancelled'
          //   }
          //   this.informService.CancelRequested(obj).then((res: any) => {
          //     if (res) {
          //       this.bsModalRef.hide();
          //       this.ngOnInit();
          //       this.comments = '';
          //       this.toaster.successToastr('Travel is Cancelled');
          //     }
          //   })
          // }
        });
      }
    } else {
      this.toaster.errorToastr('Please select any cancel reason');
    }

  }
  TravelRequestCancel() {
    this.bsModalRef.hide();
    this.comments = '';
  }
  typeaheadNoResults(event: boolean, key: any): void {
    if (event) {
      if (key === 'comments') {
        this.comments = '';
      }
    }
  }
  getTripText(type: any) {
    if (type == 'OneWay') {
      return 'One Way';
    }
    if (type == 'RoundTrip') {
      return 'Round Trip';
    }
    if (type == 'MultiCity') {
      return 'Multi City';
    }
  }
}
