import { Component, OnInit, TemplateRef } from '@angular/core';
import { Router } from '@angular/router';
import { cloneDeep } from 'lodash';
import { ToastrManager } from 'ng6-toastr-notifications';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { InfoService } from '../shared/info.service';

@Component({
  selector: 'app-manage-country',
  templateUrl: './manage-country.component.html',
  styleUrls: ['./manage-country.component.css']
})
export class ManageCountryComponent implements OnInit {
  dtOptions: DataTables.Settings = {};
  sourceData: any[] = [];
  filteredData: any[] = [];
  searchKey: string = '';
  config = {
    backdrop: true,
    class: "modal-xl",
    ignoreBackdropClick: true,
  };
  bsModalRef: BsModalRef;
  mappingData: any = {}
  locations: any;
  users: any;
  id: any;
  deletingItem: any;
  country: any = {};
  countryList: any;
  regionList: any;

  constructor(
    private infoService: InfoService,
    private router: Router,
    private toaster: ToastrManager,
    private modalService: BsModalService,
  ) { }

  ngOnInit() {
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 10,
      paging: false,
      searching: false,
      info: false,
      ordering: false
    };

    this.infoService.GetCurrentUser(() => {
      this.infoService.GetTravelProfileDetails().then((response: any) => {
        if (!(response && response.length > 0)) {
          this.router.navigate(['/MyProfile']);
          this.toaster.successToastr('Please update your profile');
        }
      });
      
      this.getRegions();
      this.getData();
    });
  }

  private getRegions() {
    this.infoService.GetAllRegions().then((res: any) => {
      this.regionList = res;
    });
  }


  getData() {
    this.infoService.getCountry().then((result: any) => {
      if (result && result.length > 0) {
        this.sourceData = result;
        this.filteredData = cloneDeep(this.sourceData)
      }
    });
  }


  filterKeyChanged() {
    this.searchKey = '';
  }

  searchKeyChanged() {
    if (this.searchKey && this.searchKey.trim().length > 0) {
      this.filteredData = this.filteredData.filter(s =>
        (s.Country && (s.Country.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.Region && (s.Region.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0))
      );
    } else {
      this.filteredData = cloneDeep(this.sourceData)
    }
  }

  openPopUp(templatePopup: TemplateRef<any>) {
    this.bsModalRef = this.modalService.show(templatePopup, this.config);
  }

  
  cancel() {
    this.country = {};
    this.bsModalRef.hide();
  }


  edit(item, MapOfficerPopup: TemplateRef<any>) {
    this.country.ID = item.ID;
    this.country.countryName = item.Country,
      this.country.region = item.Region,
    
      this.bsModalRef = this.modalService.show(MapOfficerPopup, this.config);
  }

  removeConfirmation(item: any, confirmTemplate: TemplateRef<any>) {
    if (item.IsActive) {
      this.infoService.getCityByCountryId(item.ID).then((result: any) => {
        if (result && result.length > 0) {
          this.toaster.errorToastr("Please deactivate all the cities in this country");
        } else {
          this.bsModalRef = this.modalService.show(confirmTemplate, this.config);
          this.deletingItem = item;
        
        }
    });
  } else {
    this.bsModalRef = this.modalService.show(confirmTemplate, this.config);
    this.deletingItem = item;
  
  }
  }

  remove() {
    const payload: any = {
      IsActive: !this.deletingItem.IsActive
    }
    this.infoService.updateCountry(payload, this.deletingItem.ID).then(() => {
      this.getData();
      this.denyRemoval();
      this.deletingItem.IsActive ? this.toaster.successToastr('Deactivated successfully') : this.toaster.successToastr('Activated successfully');
    });

  }

  denyRemoval() {
    this.bsModalRef.hide();
    this.id = null;
  }

  submitCountry(form) {
    const isExist = this.sourceData.find(item => item.Country.toLowerCase().trim() === this.country.countryName.toLowerCase().trim()
    && item.Region.toLowerCase().trim() === this.country.region.toLowerCase().trim());
    if (isExist) {
      this.toaster.errorToastr("Already exist");
      return;
    }

    if (form.valid) {
      const payload = {
        Country: this.country.countryName,
        Region: this.country.region,
        RegionId: this.regionList.find(item => item.RegionName === this.country.region).ID,
        IsActive:true
      }
      if(this.country.ID){
        this.infoService
        .updateCountry(payload,this.country.ID)
        .then((res: any) => {
          this.getData();
          this.cancel();
          this.toaster.successToastr('Updated successfully');
        });
      }
    else{
        this.infoService
          .InsertCountry(payload)
          .then((res: any) => {
            this.getData();
            this.cancel();
            this.toaster.successToastr('Added successfully');
          });


    }
  };
  }
}

