import { Component, OnInit, TemplateRef } from '@angular/core';
import { Router } from '@angular/router';
import { cloneDeep } from 'lodash';
import { ToastrManager } from 'ng6-toastr-notifications';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { InfoService } from '../shared/info.service';

@Component({
  selector: 'app-visa-officer-location',
  templateUrl: './visa-officer-location.component.html',
  styleUrls: ['./visa-officer-location.component.css']
})
export class VisaOfficerLocationComponent implements OnInit {
  dtOptions: DataTables.Settings = {};
  sourceData: any[] = [];
  filteredData: any[] = [];
  searchKey: string = '';
  config = {
    backdrop: true,
    class: "modal-xl",
    ignoreBackdropClick: true,
  };
  bsModalRef: BsModalRef;
  mappingData: any = {}
  locations: any;
  users: any;
  id: any;

  constructor(
    private infoService: InfoService,
    private router: Router,
    private toaster: ToastrManager,
    private modalService: BsModalService,
  ) { }

  ngOnInit() {
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 10,
      paging: false,
      searching: false,
      info: false,
      ordering: false
    };

    this.infoService.GetCurrentUser(() => {
      this.infoService.GetTravelProfileDetails().then((response: any) => {
        if (!(response && response.length > 0)) {
          this.router.navigate(['/MyProfile']);
          this.toaster.successToastr('Please update your profile');
        }
      });
      this.infoService.GetLocation().then((res: any) => {
        this.locations = res;
      });
      this.infoService.GetHrInfo().then((data: any) => {
        this.users = data;
      });
      this.getData();
    });
  }

  getData() {
    this.infoService.GetVisaOfficerLocation().then((result: any) => {
      if (result && result.length > 0) {
        this.sourceData = result;
        this.sourceData.forEach(element => {
          element.ReportingDate = new Date(element.ReportingDate);
        });
        this.filteredData = cloneDeep(this.sourceData)
      }
    });
  }

  view(id: any) {
    this.router.navigate(['/viewticketdetails', id]);
  }

  filterKeyChanged() {
    this.searchKey = '';
  }

  searchKeyChanged() {
    if (this.searchKey && this.searchKey.trim().length > 0) {
      this.filteredData = this.filteredData.filter(s =>
        (s.officer_name && (s.officer_name.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.officer_employee_id && (s.officer_employee_id.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.officer_mail_id && (s.officer_mail_id.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0)) ||
        (s.location && (s.location.toString().toLowerCase().indexOf(this.searchKey.toLowerCase()) >= 0))
      );
    } else {
      this.filteredData = cloneDeep(this.sourceData)
    }
  }

  openMapPopUp(MapOfficerPopup: TemplateRef<any>) {
    this.bsModalRef = this.modalService.show(MapOfficerPopup, this.config);
  }

  onSelectLocation(event) {
    this.mappingData.locationId = event.item.ID;
    this.mappingData.countryId = event.item.country_id;
    this.mappingData.country = event.item.country;
    this.mappingData.region = event.item.region;
    this.mappingData.regionId = event.item.region_id;
  }

  onSelectOfficer(event) {
    this.mappingData.officerId = event.item.Title;
    this.mappingData.officerMailId = event.item.maveric_email_id;
  }

  submit(form) {
    const isExist = this.sourceData.find(item => item.officer_mail_id == this.mappingData.officerMailId && item.location_id == this.mappingData.locationId);
    if(isExist) {
      this.toaster.errorToastr("Already mapped");
      return;
    }
    if (form.valid) {
      if(this.mappingData.ID) {
        const payload = {
          officer_employee_id: this.mappingData.officerId,
          officer_mail_id: this.mappingData.officerMailId,
          officer_name: this.mappingData.officer,
          location_id: this.mappingData.locationId,
          location: this.mappingData.location,
          country_id: this.mappingData.countryId,
          country: this.mappingData.country,
          region: this.mappingData.region,
          region_id: this.mappingData.regionId,
          modified_on: new Date(),
          modified_by: this.infoService.Current.EmployeeID,
          ID: this.mappingData.ID
        }
  
        this.infoService
          .UpdateVisaOfficerLocation(payload)
          .then((res: any) => {
            this.getData();
            this.cancel();
            this.toaster.successToastr('Updated successfully');
          });

      } else {
      const payload = {
        officer_employee_id: this.mappingData.officerId,
        officer_mail_id: this.mappingData.officerMailId,
        officer_name: this.mappingData.officer,
        location_id: this.mappingData.locationId,
        location: this.mappingData.location,
        country_id: this.mappingData.countryId,
        country: this.mappingData.country,
        region: this.mappingData.region,
        region_id: this.mappingData.regionId,
        is_active: true,
        created_on: new Date(),
        created_by: this.infoService.Current.EmployeeID,
        modified_on: new Date(),
        modified_by: this.infoService.Current.EmployeeID,
      }

      this.infoService
        .InsertVisaOfficerLocation(payload)
        .then((res: any) => {
          this.getData();
          this.cancel();
          this.toaster.successToastr('Added successfully');
        });
      }
    }
  };

  cancel() {
    this.mappingData = {};
    this.bsModalRef.hide();
  }

  edit(item, MapOfficerPopup: TemplateRef<any>) {
    this.mappingData.ID = item.ID;
    this.mappingData.officerId = item.officer_employee_id,
    this.mappingData.officerMailId = item.officer_mail_id,
    this.mappingData.officer = item.officer_name,
    this.mappingData.locationId = item.location_id,
    this.mappingData.location = item.location,
    this.bsModalRef = this.modalService.show(MapOfficerPopup, this.config);
  }

  removeConfirmation(item: any,confirmTemplate: TemplateRef<any>) {
    this.bsModalRef = this.modalService.show(confirmTemplate, this.config);
    this.id = item.ID;
  }

  remove() {
    this.infoService.DeleteVisaOfficerLocation(this.id).then((res: any) => {
        this.getData();
        this.denyRemoval();
        this.toaster.successToastr('Removed successfully');
      });
  }

  denyRemoval() {
    this.bsModalRef.hide();
    this.id = null;
  }
}

