import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'status'
})
export class StatusPipe implements PipeTransform {
 filterStatusArray: any[] = [];
  transform(statusarray: any[], args: string): any[] {
    if (statusarray && statusarray.length > 0 && args && args !== null) {
      if(args === 'All') {
        return statusarray;
      } else {
        this.filterStatusArray = statusarray.filter(x => x.TravelStatus === args);
        return this.filterStatusArray;
      }     
    } 
    else {
      return statusarray;
    }

  }



}
